html {
  font-size: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, AppleMyungjo;
  font-size: 1rem;
  line-height: 1.5;
}

button:disabled {
  opacity: 0.5;
}

.half_page_ish {
  width: 35%;
  height: 100%
}

.center_title {
  text-align: center;
  margin-bottom: 30px;
}

.schedule-leader {
  margin-top: 50px;
  max-height: 300px;
  display: flex;
  justify-content: center;
}

.leaderboard {
  width: 100%;
}

ul {
  padding: 0;
  list-style-type: none;
}

#logo {
  float: left;
  height: 64px;
  padding-left: 40px;
  overflow: hidden;
  line-height: 64px;
  white-space: nowrap;
  text-decoration: none;
img {
  vertical-align: middle;
  border-style: none;
}
}


.leader-board-wrapper {
  width: 90%;
}
.brandLogo {
  height: 32px;
  margin-right: 16px;
}
.brandName {
  position: relative;
  top: 1px;
  height: 16px;
  padding-right: 40px;
}

.homebody {
  margin-left: 10%;
  margin-right: 10%;
}

.shoutOutSubmit {
  margin-top: 10px;
  position: relative;
  left: 90px;
}

.achievement-avatar {
  height:  100px;
  width: 100px;
}

.attendanceGraph {
  width: 100%;
}
.submitHeart {
  top: -3px;
  position: relative;
}

.top-info {
  font-family: AppleMyungjo;
  font-size: 38px;
  margin-top: 20px;
}
.page_grid {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.hundred_x_hundred {
  float: right;
  width: 200px;
  height: 100px;
    }

.hundred_x_hundred:hover {
  position:relative;
  top:-25px;
  left:-35px;
  width:500px;
  height:auto;
  display:block;
  z-index:999;
}

.loading {
  height: 150px;
  width: 150px;
  position: fixed;
  left: 50%;
  top: 50%;
}

.half_page {
  width: 55%;
  height: 100%
}

@media screen and (max-width: 1024px) {
  .page_grid {
    display: flex;
    flex-direction: column;
  }
  .half_page_ish {
    width: 100%;
    height: 100%
  }
  .homebody {
    width: 100vw; margin: 0; padding: 0;
  }

}

@media screen and (max-width: 768px) {
  .page_grid {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .half_page_ish {
    width: 100%;
    height: 100%
  }
  .homebody {
    width: 100%;
  }
  .menu {
    visibility: hidden;
  }
}