html {
  font-size: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, AppleMyungjo;
  font-size: 1rem;
  line-height: 1.5;
}

button:disabled {
  opacity: 0.5;
}

.half_page_ish {
  width: 35%;
  height: 100%
}

.center_title {
  text-align: center;
  margin-bottom: 30px;
}

.schedule-leader {
  margin-top: 50px;
  max-height: 300px;
  display: flex;
  justify-content: center;
}

.leaderboard {
  width: 100%;
}

ul {
  padding: 0;
  list-style-type: none;
}

#logo {
  float: left;
  height: 64px;
  padding-left: 40px;
  overflow: hidden;
  line-height: 64px;
  white-space: nowrap;
  text-decoration: none;
img {
  vertical-align: middle;
  border-style: none;
}
}


.leader-board-wrapper {
  width: 90%;
}
.brandLogo {
  height: 32px;
  margin-right: 16px;
}
.brandName {
  position: relative;
  top: 1px;
  height: 16px;
  padding-right: 40px;
}

.homebody {
  margin-left: 10%;
  margin-right: 10%;
}

.shoutOutSubmit {
  margin-top: 10px;
  position: relative;
  left: 90px;
}

.achievement-avatar {
  height:  100px;
  width: 100px;
}

.attendanceGraph {
  width: 100%;
}
.submitHeart {
  top: -3px;
  position: relative;
}

.top-info {
  font-family: AppleMyungjo;
  font-size: 38px;
  margin-top: 20px;
}
.page_grid {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.hundred_x_hundred {
  float: right;
  width: 200px;
  height: 100px;
    }

.hundred_x_hundred:hover {
  position:relative;
  top:-25px;
  left:-35px;
  width:500px;
  height:auto;
  display:block;
  z-index:999;
}

.loading {
  height: 150px;
  width: 150px;
  position: fixed;
  left: 50%;
  top: 50%;
}

.half_page {
  width: 55%;
  height: 100%
}

@media screen and (max-width: 1024px) {
  .page_grid {
    display: flex;
    flex-direction: column;
  }
  .half_page_ish {
    width: 100%;
    height: 100%
  }
  .homebody {
    width: 100vw; margin: 0; padding: 0;
  }

}

@media screen and (max-width: 768px) {
  .page_grid {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .half_page_ish {
    width: 100%;
    height: 100%
  }
  .homebody {
    width: 100%;
  }
  .menu {
    visibility: hidden;
  }
}

html, body {
  height: 100%;
}
body {
  line-height: 1.5;
  font-size: 14px;
  background: #fff;
  transition: background 1s cubic-bezier(0.075,0.82,0.165,1);
  overflow-x: hidden;
}
a {
  transition: color .3s ease;
}
a:focus {
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}
.home-wrapper {
  width: 100%;
  color: #697b8c;
}
.home-wrapper .ant-btn {
  min-width: 110px;
  height: 40px;
  border-radius: 20px;
  font-size: 16px;
}
.home-wrapper .ant-btn:hover {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  box-shadow: 0 4px 12px rgba(24,144,255,0.4);
}
svg g {
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transform-box: fill-box;
}
.banner-wrapper {
  height: 526px;
  width: 100%;
  max-width: 1500px;
  margin: auto;
  position: relative;
}
.banner-wrapper .banner-title-wrapper {
  width: 40%;
  max-width: 480px;
  height: 245px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 8%;
  margin: auto;
  z-index: 1;
}
.banner-wrapper .banner-title-wrapper > * {
  will-change: transform;
}
.banner-wrapper .banner-title-wrapper h1 {
  font-family: "Futura", "Helvetica Neue For Number", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 54px;
  margin: 12px 0;
}
.banner-wrapper .banner-title-wrapper p {
  font-size: 20px;
}
.banner-wrapper .banner-title-wrapper .button-wrapper {
  margin-top: 64px;
  line-height: 40px;
  align-items: center;
  display: flex;
}
.banner-wrapper .banner-title-wrapper .button-wrapper .github-btn {
  display: inline-block;
  height: 28px;
}
.banner-wrapper .banner-title-wrapper .button-wrapper .github-btn .gh-btn {
  height: 28px;
  border-radius: 4px;
  background: #f3f3f3;
  background: linear-gradient(to bottom,#ffffff 0%,#f3f3f3 100%);
  border: 1px solid #ebedf0;
  align-items: center;
  display: flex;
  padding: 0 12px;
  font-size: 13px;
}
.banner-wrapper .banner-title-wrapper .button-wrapper .github-btn .gh-btn .gh-ico {
  margin-right: 8px;
}
.banner-wrapper .banner-title-wrapper .button-wrapper .github-btn .gh-count {
  height: 28px;
  line-height: 22px;
  background: #fff;
  border: 1px solid #ebedf0;
  border-radius: 4px;
  padding: 2px 8px;
  font-size: 13px;
}
.banner-wrapper .banner-title-wrapper .title-line {
  -webkit-transform: translateX(-64px);
          transform: translateX(-64px);
  -webkit-animation: bannerTitleLine 3s ease-in-out 0s infinite;
          animation: bannerTitleLine 3s ease-in-out 0s infinite;
}
.banner-wrapper .banner-image-wrapper {
  width: 45%;
  max-width: 598px;
  height: 324px;
  position: absolute;
  right: 8%;
  margin: auto;
  top: 0;
  bottom: 0;
  opacity: 0;
}
.home-banner-image {
  display: none;
}
.title-line-wrapper {
  height: 2px;
  width: 100%;
  overflow: hidden;
}
.title-line-wrapper .title-line {
  height: 100%;
  width: 64px;
  -webkit-transform: translateX(-64px);
          transform: translateX(-64px);
  background: linear-gradient(to right,rgba(24,144,255,0) 0%,#1890ff 100%);
}
.home-page {
  margin: 50px auto;
}
.home-page h2 {
  text-align: center;
  font-weight: 300;
  font-size: 28px;
  color: #314659;
  letter-spacing: 0.6px;
}
.home-page h2 span {
  font-weight: 600;
}
.home-page-wrapper {
  max-width: 1280px;
  width: 100%;
  margin: auto;
  position: relative;
}
.page1 {
  height: 460px;
}
.page1-line.title-line-wrapper {
  width: 312px;
  margin: 24px auto 76px;
}
.page1-line.title-line-wrapper .title-line {
  -webkit-animation: page1TitleLine 3s ease-in-out 1.5s infinite;
          animation: page1TitleLine 3s ease-in-out 1.5s infinite;
}
.page1-bg {
  font-size: 320px;
  color: #ebedf0;
  position: absolute;
  width: 100%;
  text-align: center;
  opacity: .25;
  top: 0;
  -webkit-transform: translateY(960px);
          transform: translateY(960px);
}
.page1-box-wrapper {
  margin-bottom: 62px;
  display: flex;
  align-items: flex-start;
}
.page1-box-wrapper li {
  width: 33.33%;
  display: inline-block;
  will-change: transform;
}
.page1-box-wrapper li .page1-box {
  width: 194px;
  margin: auto;
  text-align: center;
  position: relative;
}
.page1-box-wrapper li .page1-box .page1-image {
  width: 80px;
  height: 80px;
  border-radius: 40px;
  margin: 20px auto 32px;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  transition: box-shadow .3s ease-out, -webkit-transform .3s ease-out;
  transition: box-shadow .3s ease-out, transform .3s ease-out;
  transition: box-shadow .3s ease-out, transform .3s ease-out, -webkit-transform .3s ease-out;
}
.page1-box-wrapper li .page1-box:hover .page1-image {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
.page1-box-wrapper li .page1-box h3 {
  color: #0d1a26;
  font-size: 16px;
  margin: 8px auto;
}
.page1-point-wrapper {
  position: absolute;
  width: 0;
  left: 50%;
  top: 0;
}
.page1-point-wrapper .point-0 {
  width: 4px;
  height: 4px;
}
.page1-point-wrapper .point-2, .page1-point-wrapper .point-ring {
  width: 10px;
  height: 10px;
}
.page1-point-wrapper .point-ring {
  border-style: solid;
  border-width: 1px;
  background: transparent !important;
}
.page1-point-wrapper .point-1 {
  width: 6px;
  height: 6px;
}
.page1-point-wrapper .point-3 {
  width: 15px;
  height: 15px;
}
.page1-point-wrapper i {
  display: inline-block;
  border-radius: 100%;
  position: absolute;
  opacity: 0;
  -webkit-transform: translate(0,30px);
          transform: translate(0,30px);
}
.page2 {
  text-align: center;
  height: 388px;
}

.page3 {
  text-align: center;
  height: 588px;
}
.page2 .page2-content {
  will-change: transform;
}
.page2-line {
  margin: 148px auto 24px;
  width: 114px;
}
.page2-line .title-line {
  -webkit-animation: page2TitleLine 3s ease-in-out 0s infinite;
          animation: page2TitleLine 3s ease-in-out 0s infinite;
}
.page-content {
  width: 760px;
  margin: 24px auto 32px;
  line-height: 28px;
}
.home-code {
  width: 90%;
  max-width: 840px;
  border-radius: 4px;
  background: #f2f4f5;
  line-height: 28px;
  margin: 16px auto;
  color: #151e26;
  font-size: 16px;
  text-align: left;
  padding: 20px 50px;
}
.home-code span {
  color: #f5222d;
}
.home-code-comment {
  color: !important;
}
@-webkit-keyframes bannerTitleLine {
  0%, 25% {
    -webkit-transform: translateX(-64px);
            transform: translateX(-64px);
  }
  75%, 100% {
    -webkit-transform: translateX(544px);
            transform: translateX(544px);
  }
}
@keyframes bannerTitleLine {
  0%, 25% {
    -webkit-transform: translateX(-64px);
            transform: translateX(-64px);
  }
  75%, 100% {
    -webkit-transform: translateX(544px);
            transform: translateX(544px);
  }
}
@-webkit-keyframes page1TitleLine {
  0%, 25% {
    -webkit-transform: translateX(-64px);
            transform: translateX(-64px);
  }
  75%, 100% {
    -webkit-transform: translateX(376px);
            transform: translateX(376px);
  }
}
@keyframes page1TitleLine {
  0%, 25% {
    -webkit-transform: translateX(-64px);
            transform: translateX(-64px);
  }
  75%, 100% {
    -webkit-transform: translateX(376px);
            transform: translateX(376px);
  }
}
@-webkit-keyframes page2TitleLine {
  0%, 25% {
    -webkit-transform: translateX(-64px);
            transform: translateX(-64px);
  }
  75%, 100% {
    -webkit-transform: translateX(178px);
            transform: translateX(178px);
  }
}
@keyframes page2TitleLine {
  0%, 25% {
    -webkit-transform: translateX(-64px);
            transform: translateX(-64px);
  }
  75%, 100% {
    -webkit-transform: translateX(178px);
            transform: translateX(178px);
  }
}

.nav-phone-icon {
  display: none;
  position: absolute;
  right: 30px;
  top: 25px;
  z-index: 1;
  width: 16px;
  height: 22px;
  cursor: pointer;
}
